
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "TasksInput",
  emits: ["submit"],
  setup(props, { emit }) {
    let taskName = ref("");

    const handleFormSubmit = () => {
      if (taskName.value) {
        emit("submit", taskName.value);
        taskName.value = "";
      }
    };

    return {
      handleFormSubmit,
      taskName
    };
  }
});
