
import { defineComponent } from "vue";

export default defineComponent({
  name: "TasksItem",
  props: {
    task: {
      type: Object,
      required: true
    }
  },
  emits: ["toggle", "remove"],
  setup(props, { emit }) {
    const toggleTaskDone = () => {
      emit("toggle", props.task.id);
    };
    const removeTask = () => {
      emit("remove", props.task.id);
    };

    return {
      toggleTaskDone,
      removeTask
    };
  }
});
